require('./data_view.css');
var ajax_data_view = require('../../_ajax_data/data/data_list.js');
var vm_data_view = avalon.define({
    $id: 'mc_data_view',
    listArray: ajax_data_view,
    checkall_status:false,
    f_checkall_data:function (e) {
        vm_data_view.listArray.forEach(function (el) {
            el.checked = e.target.checked
        })
    },
    f_checkOne:function (e) {
        if (e.target.checked === false) {
            vm_data_view.checkall_status= false;
        } else {
            vm_data_view.checkall_status = vm_data_view.listArray.every(function(el) {
                return el.checked;
            })
        }
    },
    f_edit_checked:function (index) {
        var html_edit_data = require('./edit_data.html');
        layer.open({
            type: 1,
            skin: 'layui-layer-rim', //加上边框
            area: ['360px', '560px'], //宽高
            btn: ['保存'],
            title: '编辑信息',
            content: html_edit_data,
            yes: function() {
                layer.closeAll();
            }
        });
    }
})


