require('./user_list.css');
require('pager/pager.js');
var html_add_user = require('./add_user.html');
var ajax_user_data = require('../../_ajax_data/user/user_data.js');
var html_change_password = require('./reset_password.html');
var table_list = require('./table_list.html');
var vm_user_list = avalon.define({
    $id: 'mc_user_list',
    userArray: ajax_user_data,
    va_table_list: table_list,
    layer_addUser: html_add_user,
    f_addUser: function() {
        var ajax_city = require('../../_ajax_data/user/user_city_data.js');
        var ajax_area = require('../../_ajax_data/user/user_area_data.js');
        var vm_layer_addUser = avalon.define({
            $id: 'mc_layer_addUser',
            cityArray: ajax_city,
            areaArray: ajax_area['武汉市'].concat(),
            citySelect: '武汉市',
            areaSelect: ajax_area['武汉市']
        })
        vm_layer_addUser.$watch("citySelect", function(city) {
            vm_layer_addUser.areaArray = ajax_area[city].concat();
            vm_layer_addUser.areaSelect = vm_layer_addUser.areaArray[0].name;
        })
        layer.open({
            type: 1,
            skin: 'layui-layer-rim', //加上边框
            area: ['400px', '360px'], //宽高
            title: '创建用户',
            content: html_add_user,
            success: function() {
                avalon.scan(document.getElementById('layer_addUser'))
            }
        });
    },
    f_search: function() {

        console.log('查询');
    },
    f_reset: function() {
        console.log('重置');
    },
    f_saveUser: function() {

    },
    f_closeUser: function(index, e) {
        var status = ajax_user_data[index]['账号状态'];
        if (status === '正常') {
            ajax_user_data[index]['账号状态'] = '关闭';
        } else {
            ajax_user_data[index]['账号状态'] = '正常';
        }
    },
    f_changePassword: function() {
        layer.open({
            type: 1,
            skin: 'layui-layer-rim', //加上边框
            area: ['360px', '200px'], //宽高
            btn: ['保存'],
            title: '修改密码',
            content: html_change_password,
            yes: function() {
                layer.closeAll();
            }
        });
    },
    currentStatus: function(index) {
        var status = ajax_user_data[index]['账号状态'];
        if (status === '正常') {
            return true;
        } else {
            return false;
        }
    },
    config: {
        totalPages: 10,
        showPages: 7,
        currentPage: 1,
        is_more: true,
        is_ie: true,
        firstText: '首页',
        lastText: '末页',
        nextText: '下一页',
        prevText: '上一页'
    }
});



