require('./user_set.css');
var html_change_password = require('./change_password.html');
var ajax_city = require('../../_ajax_data/user/user_city_data.js');
var ajax_area = require('../../_ajax_data/user/user_area_data.js');
var vm_user_set = avalon.define({
    $id: 'mc_user_set',
    va_superior: '我是获取的上级单位',
    cityArray: ajax_city,
    areaArray: ajax_area['武汉市'].concat(),
    citySelect: '武汉市',
    areaSelect: ajax_area['武汉市'],
    f_changePassword: function() {
        layer.open({
            type: 1,
            skin: 'layui-layer-rim', //加上边框
            area: ['360px', '260px'], //宽高
            btn: ['保存'],
            title: '修改密码',
            content: html_change_password,
            yes: function() {
                layer.closeAll();
            }
        });
    }
})

vm_user_set.$watch("citySelect",function (city) {
    vm_user_set.areaArray = ajax_area[city].concat();
    vm_user_set.areaSelect = vm_user_set.areaArray[0].name;
})


