require('../../_config/dll.config.js');
require('./base.css');

require('avalon2');

/* 初始化页面 */
var home = require('template/home/page.html');
require('../_home/page.js');
var vm = avalon.define({
  $id: 'mc-router',
  va_username: '我是获取的',
  va_modulename: '',
  va_pagename: '',
  va_pages_styles: 'p-home',
  va_main: home
})

/* 路由控制 */
require('router/mmRouter.js');

//路由规则
avalon.router.add('/:module/:sonPage', function(moduleName, sonPage) {
  var layer = require('layer/layer.js');
  layer.config({
    btn:['提交'],
    btnAlign: 'c'
  })
  require('../../_' + moduleName + '/' + sonPage + '/page.js');
  var currPage = require('template/' + moduleName + '/' + sonPage + '.html');
  vm.va_main = currPage;
  vm.va_pages_styles = 'p-' + sonPage;
  var module_txt, page_txt;
  if (moduleName == 'user') {
    module_txt = '角色管理';
    if (sonPage == 'user_set') {
      page_txt = '账户设置';
    } else { page_txt = '用户列表'; }
  } else if (moduleName == 'subject') {
    module_txt = '科目管理';
    page_txt = '科目列表';
  } else if (moduleName == 'data') {
    module_txt = '征订录入';
    if (sonPage == 'data_entry') {
      page_txt = '数据录入';
    } else { page_txt = '查看录入'; }
  } else {
    module_txt = '报表管理';
    page_txt = '报表查看';
  }
  vm.va_modulename = module_txt;
  vm.va_pagename = page_txt;
})

//启动路由监听
avalon.history.start({
  root: "/",
  hashPrefix: ""
})


//启动扫描机制,让avalon接管页面
avalon.scan(document.body);

$(function() {
  /* 菜单滑动效果 */
  var $obj = $('#j-menu');
  $obj.find("h4").hover(function() {
    $(this).addClass("hover");
  }, function() {
    $(this).removeClass("hover");
  });
  $obj.find("p").hover(function() {
    if ($(this).hasClass("on")) {
      return;
    }
    $(this).addClass("hover");
  }, function() {
    if ($(this).hasClass("on")) {
      return;
    }
    $(this).removeClass("hover");
  });
  $obj.find("h4").click(function() {
    var $div = $(this).siblings(".list-item");
    if ($(this).parent().hasClass("selected")) {
      $div.slideUp(600);
      $(this).parent().removeClass("selected");
    }
    if ($div.is(":hidden")) {
      $("#j-menu li").find(".list-item").slideUp(600);
      $("#j-menu li").removeClass("selected");
      $(this).parent().addClass("selected");
      $div.slideDown(600);
    } else {
      $div.slideUp(600);
    }
  });

  /*下拉菜单点击显示效果*/
  $('body').on('click', '.u-btns', function() {
    $(this).children('.u-menu').toggleClass('u-menu-show');
  })
});



