require('./report_view.css');
var ajax_subject_data = require('../../_ajax_data/subject/subject_name_data.js');
var ajax_grade_data = require('../../_ajax_data/report/grade_data.js');
var ajax_other_data = require('../../_ajax_data/report/other_data.js');
var ajax_city_data = require('../../_ajax_data/user/user_city_data.js');
var ajax_area_data = require('../../_ajax_data/user/user_area_data.js');
var vm_report_view = avalon.define({
    $id: 'mc_report_view',
    checkall_subject: false,
    checkall_grade: false,
    checkall_other: false,
    checkall_unit: false,
    checkall_city: false,
    checkall_area: [],
    cityArray: ajax_city_data,
    subjectArray: ajax_subject_data,
    gradeArray: ajax_grade_data,
    otherArray: ajax_other_data,
    areaArray: [],
    areaTitArray: [],
    area_show: false,
    f_checkAll: function(type, e, index) {
        vm_report_view[type].forEach(function(el) {
            el.checked = e.target.checked;
        })
        if (type === 'cityArray') {
            if (e.target.checked === true) {
                vm_report_view.cityArray.forEach(function(city) {
                    vm_report_view.areaTitArray.ensure(city.name);
                    vm_report_view.checkall_area.ensure({ city: true });

                    vm_report_view.areaArray.ensure(ajax_area_data[city.name]);
                    vm_report_view.area_show = true;
                })
            } else {
                vm_report_view.cityArray.forEach(function(city) {
                    ajax_area_data[city.name].forEach(function(el) {
                        el.checked = true;
                    });
                });
                vm_report_view.areaTitArray.removeAll();
                vm_report_view.checkall_area.removeAll();
                vm_report_view.areaArray.removeAll();
                vm_report_view.area_show = false;
            }
        }
    },
    f_checkOne: function(allBtn, type, e, key) {


        if (e.target.checked === false) {
            vm_report_view[allBtn] = false;
            if (type == 'cityArray') {
                ajax_area_data[key].forEach(function(el) {
                    el.checked = true;
                })
                vm_report_view.area_show = !vm_report_view[type].every(function(el) {
                    return el.checked == false
                })
                var checkedStatus_index = 0
                for (var i = 0; i < vm_report_view.areaTitArray.length; i++) {
                    if (vm_report_view.areaTitArray[i] == key) {
                        checkedStatus_index = i;
                    }
                }
                vm_report_view.checkall_area.removeAt(checkedStatus_index);
                vm_report_view.areaTitArray.remove(key);
                vm_report_view.areaArray.remove(ajax_area_data[key]);
            }
        } else {
            vm_report_view[allBtn] = vm_report_view[type].every(function(el) {
                return el.checked;
            })
            if (type == 'cityArray') {
                vm_report_view.areaTitArray.push(key);
                vm_report_view.checkall_area.push({ key: true });

                vm_report_view.areaArray.push(ajax_area_data[key]);
                vm_report_view.area_show = true;
            }
        }
    },
    f_area_checkAll: function(e, index) {
        vm_report_view.areaArray[index].forEach(function(el) {
            el.checked = e.target.checked;
        })
    },
    f_area_checkOne: function(e, index) {
        avalon.log(index)
        if (e.target.checked === false) {
            vm_report_view.checkall_area.set(index, false);
        } else {
            var checkall_area_currentStatus = vm_report_view.areaArray[index].every(function(el) {
                return el.checked;
            })
            vm_report_view.checkall_area.set(index, checkall_area_currentStatus);
        }
    }
})



