require('./subject_list.css');
var ajax_subject_data = require('../../_ajax_data/subject/subject_data.js');
var ajax_subject_name_data = require('../../_ajax_data/subject/subject_name_data.js');
var html_add_subject = require('./add_subject.html');
avalon.define({
        $id: 'mc_subject_list',
        subjectArray: ajax_subject_data,
        checkStatus: false,
        f_addUser: function(title) {
            var vm_layer_addSubject = avalon.define({
                $id: 'mc_layer_addSubject',
                subjectNameArray: ajax_subject_name_data
            });
            layer.open({
                type: 1,
                skin: 'layui-layer-rim', //加上边框
                area: ['400px', '580px'], //宽高
                btn: ['提交'],
                title: title,
                content: html_add_subject,
                success: function() {
                    avalon.scan(document.getElementById('layer_addSubject'));
                },
                yes: function() {
                    layer.closeAll();
                }
            });
        },
        f_checkOne: function(i) {
            console.log(i)
        }
    })

avalon.ready(function () {


})




